<template>
  <div>
    <v-header>
      <a slot="main-nav" class="main-navbtn" @click="showMainNav"></a>
      <p slot="title" class="title">充值</p>
    </v-header>
    <div class="inpour-container">
      <div class="user-info">
        <p>账号:<span class="name">{{username}}</span></p>
        <p>余额:<span class="account">{{bean}} 欢朋币</span></p>
      </div>
      <div class="inpour-wrap">
        <p class="hint">请选择充值数额</p>
        <ul class="inpour-list">
          <li v-for="price in inpourList" :class="{ 'active': price === selectPrice }" @click="selectPrice = price">
            <p>{{price * 10}} 欢朋币</p>
            <p>￥{{price}}</p>
          </li>
        </ul>
      </div>
      <div class="pay-mode">
        <p class="hint">请选择支付方式</p>
        <ul class="pay-mode-list">
          <li class="list-item" @click="payMode=1">
            <div class="item-wrap">
              <img src="../../../static/images/icon_Alipay@2x.png" alt="icon_Alipay">
              <div class="item-right">
                <p class="mode-name">支付宝</p>
                <div class="pay-mode-btn" :class="{'check':payMode === 1,'uncheck':payMode===2}"></div>
              </div>
            </div>
          </li>
          <li class="list-item" @click="payMode=2">
            <div class="item-wrap">
              <img src="../../../static/images/icon_WechatPay@2x.png" alt="icon_WechatPay">
              <div class="item-right">
                <p class="mode-name">微信支付</p>
                <div class="pay-mode-btn" :class="{'check':payMode === 2,'uncheck':payMode===1}"></div>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <p class="price">售价: <span>{{selectPrice}}</span><span>元</span></p>
      <div class="live-pay-wrap">
        <button class="live-pay-btn">立即充值</button>
        <div class="pay-mode-btn" :class="{'check':agreement === true,'uncheck':agreement===false}" @click="agreement=!agreement"></div>
        <p>我已阅读并同意 <a href="http://www.huanpeng.com/protocol/mobileLivePay.html">《欢朋充值服务协议》</a> </p>
      </div>
    </div>
    <main-nav ref="mainNav"></main-nav>
  </div>
</template>

<script>
  import vHeader from '@/components/header/header';
  import mainNav from '@/components/nav/main-nav';

  export default {
    name: 'inpour',
    data() {
      return {
        username: '',
        bean: '',
        inpourList: [10, 20, 50, 100, 500, 1000],
        selectPrice: 10,
        inpourAmount: 10,
        payMode: 1,
        AliBtnStyle: 'check',
        WeChectBtnStyle: 'uncheck',
        agreement:true,
      }
    },
    created() {
      this.getBeanJson();
    },
    methods: {
      // 获取用户欢朋币信息
      getBeanJson() {
        this.$ajax.get('/api/getBeanJson').then((res) => {
          this.username = res.data.name;
          this.bean = res.data.bean;
          console.log(this.username, this.bean);
        })
      },
      // 展示用户导航
      showMainNav() {
        this.$refs.mainNav.show();
      },
    },
    components: {mainNav, vHeader}
  }
</script>

<style lang="less" scoped>
  @myrem: 75rem;
  @color-gray: rgb(146, 146, 146);
  @color-black: rgb(40, 40, 40);
  @color-orange: rgb(255, 120, 0);
  @color-white: rgb(255, 255, 255);
  .inpour-container {
    padding-top: 88/@myrem;
    height: 100%;
  }

  .user-info {
    padding: 54/@myrem 32/@myrem;
    display: flex;
    justify-content: flex-start;
    p {
      width: 343/75rem;
      font-size: 30/@myrem;
      color: @color-gray;
    }
    span {
      display: inline-block;
      vertical-align: top;
      width: 80%;
      padding-left: 20/@myrem;
    }
    .name {
      color: @color-black;
    }
    .account {
      color: @color-orange
    }
  }

  .inpour-wrap {
    width: 100%;
    padding: 0 32/@myrem 40/@myrem 32/@myrem;
    .hint {
      padding-bottom: 28/@myrem;
      font-size: 28/@myrem;
      color: @color-gray;
    }
    .inpour-list {
      width: 100%;
      height: 224/@myrem;
      display: flex;
      justify-content: space-between;
      align-content: space-between;
      flex-wrap: wrap;
      li {
        border-radius: 10/@myrem;
        width: 218/@myrem;
        height: 96/@myrem;
        background-color: @color-white;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        font-size: 30/@myrem;
        color: @color-black;
        p:nth-of-type(2){
          font-size: 24/@myrem;
          color: @color-gray;
        }
      }
      .active {
        border: 2/@myrem solid @color-orange;
        color: @color-orange;
        p:nth-of-type(2){
          color: @color-orange;
        }
      }
    }
  }

  .pay-mode {
    width: 100%;
    padding: 0 32/@myrem 40/@myrem 32/@myrem;
    .hint {
      padding-bottom: 28/@myrem;
      font-size: 28/@myrem;
      color: @color-gray;
    }
    .pay-mode-list {
      width: 100%;
      .list-item {
        width: 100%;
        height: 104/@myrem;
        display: flex;
        align-items: center;
        background-color: @color-white;
        font-size: 32/@myrem;
        &:nth-of-type(1) {
          border-top-left-radius: 15/@myrem;
          border-top-right-radius: 15/@myrem;
        }
        &:nth-of-type(2) {
          border-bottom-left-radius: 15/@myrem;
          border-bottom-right-radius: 15/@myrem;
        }
      }
      .item-wrap {
        width: 100%;
        height: 48/@myrem;
        padding: 0 32/@myrem;
        display: flex;
        img {
          height: 100%;
          padding-right: 32/@myrem;
        }
        .item-right {
          width: 100%;
          height: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          .mode-name {
            font-size: 32/@myrem;
            color: @color-black;
          }
        }
      }
    }
  }

  .pay-mode-btn {
    width: 40/@myrem;
    height: 40/@myrem;
    border-radius: 50%;
  }
  .uncheck {
    background: url("../../../static/images/LoginAndRegister_button_alreadyRead_normal@2x.png") no-repeat;
    background-size: contain;
    background-position: center;
  }
  .check {
    background: url("../../../static/images/LoginAndRegister_button_alreadyRead_highlighted@2x.png") no-repeat;
    background-size: contain;
    background-position: center;
  }

  .price {
    padding-left: 32/@myrem;
    padding-bottom: 30/@myrem;
    font-size: 28/@myrem;
    color: @color-gray;
    span {
      color: @color-orange;
      &:first-child {
        font-size: 40/@myrem;
        padding-right: 5/@myrem;
      }
    }
  }

  .live-pay-wrap {
    padding: 0 32/@myrem;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .live-pay-btn {
      width: 100%;
      height: 96/@myrem;
      border: 0;
      background-color: @color-orange;
      border-radius: 30/@myrem;
      font-size: 36/@myrem;
      color: @color-white;
      &:active{
        background-color: rgb(255, 108, 9);
      }
    }
    .pay-mode-btn{
      position: relative;
      top:24/@myrem;
    }
    p{
      position: relative;
      top:30/@myrem;
      left:16/@myrem;
      font-size: 28/@myrem;
      color: @color-gray;
      a{
        color: @color-orange;
      }
    }
  }

</style>



