import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/page/home/home'
import Hot  from '@/page/home/Hot/Hot'
import Follow from '@/page/home/Follow/Follow'


import Classify from '@/page/classify/classify'
import Zone from '@/page/zone/zone'
import Inpour from '@/page/inpour/inpour'
import User from '@/page/user/user'

import WebView from '@/page/webview/webview'

Vue.use(Router)

export default new Router({
  linkActiveClass:'active',
  routes: [
    {
      path: '*',
      redirect: '/home/Hot'
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      children:[
        { path: '/home/Hot', name: 'home_Hot', component: Hot},
        { path: '/home/Follow', name: 'home_Follow', component: Follow}
      ]
    },
    {
      path: '/classify',
      name: 'classify',
      component: Classify
    },
    {
      path: '/zone',
      name: 'zone',
      component: Zone
    },
    {
      path: '/inpour',
      name: 'inpour',
      component: Inpour
    },
    {
      path: '/user',
      name: 'uesr',
      component: User
    },
    {
      path: '/webview/:id',
      name: '/webview',
      component: WebView
    }
  ]
})



