<template>
  <div class="home-container">
    <v-swiper class="swiper-container" v-if="swiperList.length">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="item in swiperList">
          <router-link :to="'/webview/' + item.url.replace(/\//g,'*hp*')" class="web-link">
            <img v-lazy="item.poster" alt=""/>
          </router-link>
        </div>
      </div>
    </v-swiper>
    <div class="live-container" v-for="(item,index) in hotList">
      <section class="live-info">
        <img v-lazy="item.icon" alt=""/>
        <span>{{item.gameName}}</span>
        <a href="javascript:void(0)" v-if="index !== 0">
          <p class="more">更多</p>
        </a>
      </section>
      <video-list :video-list="item.liveList"></video-list>
    </div>
    <!-- <div class="live-container">
      <section class="live-info">
        <img src="../../../static/images/king_glory.png" alt="king-glory">
        <span>王者荣耀</span>
        <a href="javascript:void(0);" v-if="seen">
          <p class="more">更多</p>
        </a>
      </section>
      <video-list :posterInfo="liveItems"></video-list>
    </div> -->
  </div>
</template>

<script>
  import vSwiper from 'components/swiper/swiper';
  import videoList from 'components/videoList/videoList'

  const ERR_OK = '1';
  export default {
    name: 'home_Hot',
    data() {
      return {
        seen: true,
        swiperList: [],
        hotList: [],
      }
    },
    computed: {
      numberFormat() {
        return this.$common.numberFormat
      }
    },
    created() {
      this.getHotData();
      this.getHotListData();
      // this.getTest();
    },
    mounted() {
    },
    methods: {
      getHotData() {
        const self = this;
        this.$ajax.post('/api/other/getCarousel.php', {
          client: '3'
        }).then((res) => {
          const response = res.data;
          if (response.status == ERR_OK) {
            self.swiperList = response.content.list;
//            console.log('sp', self.swiperList);
          }
        });
      },
      getHotListData() {
        this.$ajax.post('/api/app/indexForApp.php', {
          uid: '1860',
          encpass: '9db06bcff9248837f86d1a6bcf41c9e7'
        }).then((res) => {
          this.hotList = res.data.content.list;
//          console.log('res hotListJson: ', this.hotList);
        });
      }
    },
    components: {vSwiper, 'video-list': videoList}
  }

</script>

<style lang="less" scoped>
  @myrem: 75rem;

  .live-container {
    margin-top: -20/@myrem;
  }

  .live-info {
    width: 100%;
    height: 80/@myrem;
    padding: 0 32/@myrem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    img {
      display: block;
      height: 56/@myrem;
    }
    span {
      display: block;
      color: rgb(40, 40, 40);
      font-size: 32/@myrem;
      padding-left: 16/@myrem;
    }
    a {
      display: block;
      position: relative;
      left: 60%;
      .more {
        font-size: 24/@myrem;
        color: rgb(146, 146, 146);
        &::after {
          content: '';
          width: 10/@myrem;
          height: 10/@myrem;
          border-top: 3/@myrem solid rgb(204, 204, 204);
          border-right: 3/@myrem solid rgb(204, 204, 204);
          display: inline-block;
          margin-left: 3/@myrem;
          margin-bottom: 3/@myrem;
          transform: rotate(45deg);
        }
      }
    }
  }
</style>



