<template>
  <transition enter-active-class="animated fast fadeInLeft" leave-active-class="animated fast fadeOutLeft">
    <div class="nav-box" v-show="navShow" @touchstart="slideStart" @touchend="slideEnd" @touchmove.prevent>
      <div class="wrapper">
        <section class="nav-user blur-container" v-show="uid">
          <div class="blur-bg"></div>
          <div class="nav-user-photo">
            <div class="bg"></div>
            <!-- <img src="./user_photo_test.png" alt="user photo"> -->
          </div>
          <div class="nav-user-name">{{uid}}</div>
          <div class="nav-user-asset">
            <div class="asset-info">
              <div class="icon icon-coin"></div>
              <p>560</p>
            </div>
            <div class="asset-info">
              <div class="icon icon-bean"></div>
              <p>560</p>
            </div>
          </div>
        </section>
        <section class="nav-user login-area" v-show="!uid">
          <a href="/h5login/index.html" class="btn login-btn">登陆</a>
          <a href="/h5login/register.html" class="btn sign-in-btn">注册</a>
        </section>
        <ul class="nav-main">
          <li class="nav-item">
            <router-link to="/home/Hot" class="nav-link">
              <div class="icon icon-home"></div>
              <p class="nav-title">首页</p>
            </router-link>
          </li>
          <li class="nav-item">
            <router-link to="/classify" class="nav-link">
              <div class="icon icon-catalog"></div>
              <p class="nav-title">分类</p>
            </router-link>
          </li>
          <li class="nav-item">
            <router-link to="/inpour" class="nav-link">
              <div class="icon icon-inpour"></div>
              <p class="nav-title">充值</p>
            </router-link>
          </li>
          <li class="nav-item">
            <router-link to="/user" class="nav-link">
              <div class="icon icon-app"></div>
              <p class="nav-title">APP下载</p>
            </router-link>
          </li>
  
        </ul>
        <section class="nav-userout" @click="logout" v-show="uid">
          <div class="icon icon-logoff"></div>
          <p class="userout-title">注销</p>
        </section>
        <h2 class="copyright">COPYRIGNT © 2017 HUANPENG.COM</h2>
      </div>
  
      <div class="back-box" @click="hide"></div>
      <v-dialog ref="dialog" @emitSubmit="userLogout">
        <p>确定注销吗？</p>
      </v-dialog>
    </div>
  </transition>
</template>

<script>
import { mapGetters, mapActions, mapMutations} from 'vuex';
import { getCookie } from 'assets/js/common';
import vDialog from 'components/dialog/dialog';

export default {
  name: 'mainNav',
  data() {
    return {
      navShow: false,
      touchStartX: 0,
      touchEndX: 0
    }
  },
  computed: {
    ...mapGetters([
      'uid',
      'encpass'
    ])
  },
  created() {
    this.commitUserInfo()
  },
  mounted() {
  },
  methods: {
    hide() {
      this.navShow = false;
    },
    show() {
      this.navShow = true;
    },
    slideStart(e) {
      this.touchStartX = e.changedTouches[0].screenX;
    },
    slideEnd(e) {
      this.touchEndX = e.changedTouches[0].screenX;
      if (this.touchStartX - this.touchEndX > 30) {
        this.navShow = false;
      }
    },
    login() {
      this.userLogin({ uid: '1860', encpass: '9db06bcff9248837f86d1a6bcf41c9e7' })
    },
    logout() {
      this.$refs.dialog.show();
    },
    commitUserInfo() {
      const uid = getCookie('_uid')||'1860';
      const encpass = getCookie('_enc')||'9db06bcff9248837f86d1a6bcf41c9e7';
      this.setUid(uid);
      this.setEncpass(encpass);
    },
    ...mapMutations({
      setUid: 'SET_UID',
      setEncpass: 'SET_ENCPASS'
    }),
    ...mapActions([
      'userLogin',
      'userLogout'
    ])
  },
  components: { vDialog }
}
</script>

<style lang="less" scoped>
@import '~assets/css/common';
@import '~assets/css/animate';

@keyframes mask-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.slide-enter-active,
.slide-leave-active {
  transition: all .4s ease;
}

.slide-enter,
.slide-leave-to {
  transform: translateX(-100%);
}

.nav-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  display: flex;
  .back-box {
    flex: 0 0 142/@myrem;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    animation: mask-in 1s;
    animation-delay: .4s;
    animation-fill-mode: forwards;
    transform: translate3d(0, 0, 0);
  }
  .wrapper {
    height: 100%;
    flex: 0 0 608/@myrem;
    background: rgb(255, 255, 255);
  }
}

.blur-container {
  background-color: rgba(39, 64, 139, .5);
  overflow: hidden;
  .blur-bg {
    width: 100%;
    height: 100%;
    background: url("../../../static/images/home_test/profile_photo_4.png") no-repeat;
    background-size: cover;
    background-position: center;
    filter: blur(20/@myrem) opacity(20%);
    transform: translate3d(0, 0, 0);
  }
}

.login-area {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(39, 64, 139, .45);
  .btn {
    width: 140/@myrem;
    height: 70/@myrem;
    margin: 0;
    padding: 0;
    border: 0;
    border-radius: 10/@myrem;
    font-size: 30/@myrem;
  }
  .login-btn {
    background-color: rgb(255, 120, 0);
    color: rgb(255, 255, 255);
  }
  .sign-in-btn {
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
  }
}

.nav-user {
  height: 344/@myrem;
  position: relative;
  color: rgb(255, 255, 255);
  .nav-user-photo {
    position: absolute;
    top: 32/@myrem;
    left: 32/@myrem;
    .bg {
      width: 120/@myrem;
      height: 120/@myrem;
      border: 4/@myrem solid rgb(232, 234, 235);
      border-radius: 50%;
      background-image: url('./user_photo_test.png');
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
    } // img {
    //   display: block;
    //   width: 120/@myrem;
    //   height: 120/@myrem;
    //   border: 4/@myrem solid rgb(232, 234, 235);
    //   border-radius: 50%;
    //   transform: translate3d(0, 0, 0);
    // }
  }
  .nav-user-name {
    position: absolute;
    left: 32/@myrem;
    top: 192/@myrem;
    width: 290/@myrem;
    font-size: 32/@myrem;
    color: rgb(255, 255, 255);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  .nav-user-asset {
    position: absolute;
    left: 32/@myrem;
    right: 0;
    bottom: 24/@myrem;
    display: flex;
    .asset-info {
      display: flex;
      align-items: center;
      height: 48/@myrem;
      width: 288/@myrem;
      .icon-coin {
        background-image: url("../../../static/images/icon_huanpeng_coin.png");
      }
      .icon-bean {
        background-image: url("../../../static/images/icon_huanpeng_bean.png");
      }
      p {
        padding-left: 16/@myrem;
        width: 100%;
        font-size: 28/@myrem;
        color: rgb(255, 255, 255);
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
}

.nav-main {
  height: 420/@myrem;
  padding: 16/@myrem 0;
  border-bottom: 2/@myrem solid rgb(235, 235, 235);
  .nav-item {
    height: 96/@myrem;
  }
}

.icon {
  width: 48/@myrem;
  height: 48/@myrem;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.nav-link {
  width: 100%;
  height: 100%;
  padding-left: 32/@myrem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .icon-home {
    background-image: url("../../../static/images/icon_home.png");
  }
  .icon-catalog {
    background-image: url("../../../static/images/icon_catalog.png");
  }
  .icon-inpour {
    background-image: url("../../../static/images/icon_inpour.png");
  }
  .icon-app {
    background-image: url("../../../static/images/icon_app.png");
  }
  .nav-title {
    padding-left: 64/@myrem;
    font-size: 28/@myrem;
    color: rgb(80, 80, 80);
  }
}

.nav-link.active {
  .icon-home {
    background-image: url("../../../static/images/icon_home_h.png");
  }
  .icon-catalog {
    background-image: url("../../../static/images/icon_catalog_h.png");
  }
  .icon-inpour {
    background-image: url("../../../static/images/icon_inpour_h.png");
  }
