  }
  .nav-title {
    color: rgb(255, 120, 0);
  }
}

.nav-userout {
  width: 100%;
  height: 96/@myrem;
  padding-left: 32/@myrem;
  padding-top: 16/@myrem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  .icon-logoff {
    background-image: url("../../../static/images/icon_logoff.png");
  }
  .userout-title {
    padding-left: 64/@myrem;
