<template>
  <div>
  	<v-header>
		<a slot="main-nav" class="go-backbtn" @click="goBack"></a>
	  	<p slot="title" class="title">游戏专区</p>
    </v-header>
    
    <div class="zone-container">
    	<video-list :videoList="gameList" :hasGameTitle="hasGameTitle">
    		
    	</video-list>
    	<div class="pagebtn-box">
    		<a @click="nextPage" class="next-page">下一页</a>
    	</div>
    </div>
    
    <main-nav ref="mainNav"></main-nav>
  </div>
</template>

<script>
import vHeader from '@/components/header/header';
import mainNav from '@/components/nav/main-nav';
import videoList from '@/components/videoList/videoList';
import qs from 'qs';

export default {
	name: 'zone',
  components: { vHeader,mainNav,videoList },
	data() {
		return{
			gameList:[],
			hasGameTitle: false,
			page:2
		}
	},
	mounted() {
		this.init();
		console.log(qs);
	},
	methods:{
		init(){
			const self = this;
		    this.$ajax.post('/api/app/gameInfoForApp.php',qs.stringify({
			  'gameID': self.$route.query.id,
			  'type': '1'
			})).then((res)=> {
		 		self.gameList = res.data.content.liveList;
		    });
		},
		nextPage(gameList){
			var i = this.page ++;
			const self = this;
		    this.$ajax.post('/api/app/gameInfoForApp.php',qs.stringify({
			  'gameID': self.$route.query.id,
			  'type': '1',
			  'page': i
			})).then((res)=> {
		 		self.ListAdd = res.data.content.liveList;
		 		var liveTotal = res.data.content.total;
		 		var pageTotal = Math.ceil(liveTotal/8);
		 		console.log(pageTotal);
		 		var gameListAdd = self.ListAdd;
		 		for(var j=0;j<gameListAdd.length;j++){
		 			if(i <= pageTotal){
		 				self.gameList.push(gameListAdd[j]);
		 			};
		 		};	
		  });
		},
		goBack(){
			history.back();
		}
	}
}

</script>

<style lang="less" scoped>
@myrem :75rem;
	.go-backbtn{
		width: 56/@myrem;
		height: 56/@myrem;
		position: absolute;
		left: 32/@myrem;
		top: 16/@myrem;
		background:url(../../../static/images/icon_back.png) no-repeat;
		background-size: 56/@myrem;
	}
	.go-backbtn:active{
		background:url(../../../static/images/icon_back_h.png) no-repeat;
	}
	.zone-container{
		height: 100%;
		margin-top: 105/@myrem;
		.pagebtn-box{
			text-align: center;
			margin: 20/@myrem auto;
			.next-page{
				font-size: 28/@myrem;
			}
		}
	}
</style>


