<template>
  <div>
    <!--header-->
    <v-header class="home-title-box">
      <div slot="main-nav" class="nav-button" @click="showMainNav"></div>
      <p slot="title" class="home-title">
        <router-link to="/home/Hot" class="link">热门</router-link>
      </p>
      <p slot="title" class="home-title">
        <router-link to="/home/Follow" class="link">关注</router-link>
      </p>
      <div slot="search" class="search-button"></div>
    </v-header>
    <!--Hot Follow 组件-->
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <!--footer组件-->
    <main-nav ref="mainNav"></main-nav>
  </div>
</template>

<script>
  import vHeader from '@/components/header/header';
  import mainNav from '@/components/nav/main-nav';

  export default {
    name: 'home',
    components: {mainNav, vHeader},
    data() {
      return {
        // showNav: false
      }
    },
    methods: {
      // 展示用户导航
      showMainNav() {
        this.$refs.mainNav.show();
      }
    }
  }

</script>

<style lang="less" scoped>
  @myrem: 75rem;
  .home-title-box {
    width: 100%;
    padding: 0 32/@myrem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .nav-button {
      width: 56/@myrem;
      height: 56/@myrem;
      background: url("../../../static/images/logo.png") no-repeat;
      background-size: contain;
    }
    .search-button {
      width: 56/@myrem;
      height: 56/@myrem;
      background: url("../../../static/images/icon_search.png") no-repeat;
      background-size: contain;
      &:active {
        background: url("../../../static/images/icon_search_h.png") no-repeat;
        background-size: contain;
      }
    }
    .nav-button, .search-button {
      img {
        display: block;
        width: 56/@myrem;
        height: 56/@myrem;
      }
    }
    .home-title {
      width: 168/@myrem;
      text-align: center;
      display: inline-block;
      .link {
        width: 100%;
        display: block;
        position: relative;
        z-index: 5;
        font-size: 40/@myrem;
        color: rgb(40, 40, 40);
      }
      .link.active {
        color: rgb(255, 120, 0);
        &:after {
          content: '';
          width: 0;
          height: 0;
          border-width: 10/@myrem 20/@myrem;
          border-style: solid;
          border-color: #ff7800 transparent transparent transparent;
          position: absolute;
          left: 50%;
          top: 115%;
          margin-left: -20/@myrem;
        }
      }
    }
  }

  .home-container {
    padding-top: 88/@myrem;
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: rgb(245, 245, 245);
  }
</style>



