<template>
<transition leave-active-class="animated fadeOut">
  <div class="dialog" v-show="isShow">
    <transition name="dialogShow" enter-active-class="animated bounceIn" leave-active-class="animated bounceOut">
      <div class="dialog-container" v-show="isShow">
      	<slot></slot>
      	<div class="button-wrapper" >
  	      <button class="btn btn-yes" @click="emitSubmit">确定</button>
  	      <button class="btn btn-cancel" @click="hide">取消</button>
  	    </div>
      </div>
    </transition>
  </div>
</transition>
  

</template>

<script>
export default {
  name: 'dialog',
  data() {
  	return {
  	  isShow: false
  	}
  },
  methods: {
  	show() {
  	  this.isShow = true;
  	},
  	hide() {
  	  this.isShow = false;
  	},
  	emitSubmit() {
  	  this.isShow = false;
  	  this.$emit('emitSubmit');
  	}
  }
}
</script>

<style lang="less">
@import '~assets/css/animate';
@import '~assets/css/common';


.dialog {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.dialog-container {
  width: 90%;
  background: #fff;
  padding: 40/@myrem 0;
  border-radius: 6px;
  p {
  	font-size: 40/@myrem;
  	text-align: center;
  	margin-bottom: 30/@myrem;
  }
}
.button-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
  .btn {
	flex: 0 1 40%;
	width: 40%;
	height: 80/@myrem;
	font-size: 32/@myrem;
	border-radius: 2px;
	&.btn-yes {
	  background: @hpColor;
	  color: #fff;
	}
	&.btn-cancel {
	  color: #666;
	  background: #ddd;
	}
  }
}
</style>


